import { MONEY_NAME_STRING } from '../../constants';

export const purchaseTabTemplate = /* html */ `
<section class="form-section" aria-labelledby="purchase-tab-title">
  <h2 class="tab-title" id="purchase-tab-title"> </h2>
  
  <form id="user-money-form">
    <div class="instructions">
      <h3>    </h3>
      <ul class="instructions-list">
        <li>  10,  10000   .</li>
        <li>              .</li>
        <li>         .</li>
      </ul>
    </div>
    <label for="user-money">   </label>
    <div class="input-form-wrapper">
      <input type="number" id="user-money-input" placeholder="" name="user-money" min="10" max="10000" step="10" required/>
      <button type="submit" class="submit-button"></button>
    </div>
  </form>
  <p> : <span id="total-insert">0</span></p>
</section>
<table class="product-status-table">
  <caption>
       
  </caption>
  <tr>
    <th></th>
    <th></th>
    <th></th>
    <th></th>
  </tr>
</table>
<table class="coin-status-table">
  <caption>
     
  </caption>
  <tr>
    <th></th>
    <th></th>
  </tr>
  <tr>
    <td>500</td>
    <td data-coin-name='${MONEY_NAME_STRING.COIN_500_WON}'>0</td>
  </tr>
  <tr>
    <td>100</td>
    <td data-coin-name='${MONEY_NAME_STRING.COIN_100_WON}'>0</td>
  </tr>
  <tr>
    <td>50</td>
    <td data-coin-name='${MONEY_NAME_STRING.COIN_50_WON}'>0</td>
  </tr>
  <tr>
    <td>10</td>
    <td data-coin-name='${MONEY_NAME_STRING.COIN_10_WON}'>0</td>
  </tr>
</table>
<button type="button" id="return-change-button"></button>
`;

export const purchaseProductTableRowTemplate = ({
  name,
  price,
  stock,
  id,
}) => /* html */ `
  <tr class="product-table-row">
    <td class="product-name">${name}</td>
    <td class="product-price">${price}</td>
    <td class="product-stock">${stock}</td>
    <td>
      <div class="table-button-wrapper">
        <button type="button" class="purchase-product-button" data-product-id="${id}"></button>
      </div>
    </td>
  </tr>
  `;
