import { MONEY_NAME_STRING } from '../../constants';

const chargeTabTemplate = /* html */ `
<section class="form-section" aria-labelledby="charge-tab-title">
  <h2 class="tab-title" id="charge-tab-title"> </h2>
  <form id="add-change-form">
    <div class="instructions">
      <h3>     </h3>
      <ul class="instructions-list">
        <li>  : 10</li>
        <li>   : 100,000</li>
      </ul>
    </div>
    <label for="change">   </label>
    <div class="input-form-wrapper">
      <input type="number" id="money-input" placeholder="" name="change" min="10" max="100000" step="10" required/>
      <button type="submit" class="submit-button"></button>
    </div>
  </form>
  <p>  : <span id="total-change">0</span></p>
</section>
<table class="coin-status-table">
  <caption>
      
  </caption>
  <tr>
    <th></th>
    <th></th>
  </tr>
  <tr>
    <td>500</td>
    <td data-coin-name='${MONEY_NAME_STRING.COIN_500_WON}'>0</td>
  </tr>
  <tr>
    <td>100</td>
    <td data-coin-name='${MONEY_NAME_STRING.COIN_100_WON}'>0</td>
  </tr>
  <tr>
    <td>50</td>
    <td data-coin-name='${MONEY_NAME_STRING.COIN_50_WON}'>0</td>
  </tr>
  <tr>
    <td>10</td>
    <td data-coin-name='${MONEY_NAME_STRING.COIN_10_WON}'>0</td>
  </tr>
</table>
`;

export default chargeTabTemplate;
