const loginPageTemplate = /* html */ `
  <h2></h2>
  <form class="auth-form">
    <label for="email"></label>
    <input
      type="email"
      name="email"
      id="email-input"
      placeholder=" ."
      required
    />
    <label for="password"></label>
    <input
      type="password"
      name="password"
      id="password-input"
      placeholder=" ."
      required
    />
    <button type="submit" class="submit-button"></button>
    <p>  ? <a href="#/register" id="register-page-link"></a></p>
  </form>
`;

export default loginPageTemplate;
