export const productTabTemplate = /* html */ `
<section class="form-section" aria-labelledby="product-tab-title">
  <h2 class="tab-title" id="product-tab-title"> </h2>
  <form id="add-product-form">
    <div class="instructions">
      <h3>    </h3>
      <ul class="instructions-list">
        <li>: 1  10 </li>
        <li>: 100  10000 , 10 </li>
        <li>: 1  20 </li>
      </ul>
    </div>
    <fieldset>
      <legend>   .</legend>
      <div class="input-wrapper">
        <label for="name">
        <input type="text" name="name" id="product-name-input" placeholder="" required/>
        </label>
      </div>
      <div class="input-wrapper">
        <label for="price"> </label>
        <input type="number" name="price" id="product-price-input" placeholder="" min="100" max="10000" required/>
      </div>
      <div class="input-wrapper">
        <label for="stock"> </label>
        <input type="number" name="stock" id="product-stock-input" placeholder="" min="1" max="20" required/>
      </div>
      <button type="submit" class="submit-button"></button>
    </fieldset>
  </form>
</section>
<table class="product-status-table">
  <caption>
     
  </caption>
  <tr>
    <th></th>
    <th></th>
    <th></th>
    <th></th>
  </tr>
</table>
`;

export const productTableRowTemplate = ({ name, price, stock, id }) => /* html */ `
<tr class="product-table-row">
  <td class="product-name">${name}</td>
  <td class="product-price">${price}</td>
  <td class="product-stock">${stock}</td>
  <td>
    <div class="table-button-wrapper">
      <button type="button" class="update-product-button" data-product-id=${id}></button>
      <button type="button" class="remove-product-button" data-product-id=${id}></button>
    </div>
  </td>
</tr>
`;

export const productUpdateTableRowTemplate = ({ name, price, stock, id }) => /* html */ `
<tr>
  <td>
    <input type="text" class="update-product-name-input" value="${name}" />
  </td>
  <td>
    <input type="number" class="update-product-price-input" value="${price}" />
  </td>
  <td>
    <input type="number" class="update-product-stock-input" value="${stock}" />
  </td>
  <td>
    <div class="table-button-wrapper">
      <button type="button" class="confirm-update-button" data-product-id=${id}>
      
      </button>
      <button type="button" class="cancel-update-button" data-product-id=${id}>
      
      </button>
    </div>
  </td>
</tr>
`;
